import React from 'react';
import Title from '../../general/title';
import './coronavirus.css';
import { faqs } from './coronavirus.json';

const Coronavirus = () => (
    <div className='coronavirus-page'>
        {
            faqs.map(faq => (
                <div>
                    <p>{faq.question}</p>
                    <p>{faq.answer}</p>
                </div>
            ))
        }
    </div>
)

export default Coronavirus;